<template>
  <div class="pay-details">

    <TheHeader title=""
               isUnderline></TheHeader>

    <ActivityDetail :productData="productData"
                    :standard="standard"></ActivityDetail>

    <div class="wx-payWay"
         v-if="productData.allIntegral">
      <div class="wx-payWay__row wx-payWay__title">
        <div class="wx-payWay__leftTitle"></div>
        <div class="wx-payWay__rightContent color-theme">{{ productData.allIntegral }}</div>
      </div>

      <ThePayCheckList :productData="productData"
                       :amount="$route.params.buyNum | parseInt"
                       v-model="payWayIndex"></ThePayCheckList>
    </div>

    <IButton size="large"
             type="red"
             style="border-radius: 0; position: fixed; bottom: 0;"
             @click="onPay()"></IButton>
  </div>
</template>

<script>
// import { TheHeader, ThePayCheckList } from '@/components/index';
import { TheHeader } from '@/components/index';
import ThePayCheckList  from './ThePayCheckList/ThePayCheckList';
import ActivityDetail from './ActivityDetail';
import * as service from '@/api/service';
import wx from 'weixin-js-sdk';
import _ from 'lodash';

export default {
  name: 'associationActivityPayDetail',

  components: {
    TheHeader,
    ThePayCheckList,
    ActivityDetail,
  },

  data() {
    return {
      productData: {
        info: {
          address: '',
          lessonArr: [],
        },
      },
      standard: {},
      payWayIndex: -1,
      uid: 0,
      paryOrderID: '',
      reurl: '',
    };
  },

  mounted() {
    // 
    this.$nextTick(() => {
      this.reurl = window.location.href;
      this.getData();
    });
  },

  methods: {
    // 
    async getData() {
      try {
        // 
        const data = await service.lessonDetail({
          id: this.$route.params.id,
        });

        this.uid = data.uid;
        this.productData = data.result;
        const { lessonArr } = this.productData.info;

        // id
        for (let i = 0; i < lessonArr.length; i += 1) {
          if (this.$route.params.standardID === lessonArr[i].id) {
            this.standard = lessonArr[i];
          }
        }
      } catch (error) {
        console.log('error', error);
      }
    },

    // 
    onPay: _.debounce(
      async function onPay() {
        let tips = '';
        const {
          integral,
          money,
          title,
          mixIntegral,
          mixMoney,
        } = this.productData.info;
        const { buyNum } = this.$route.params;

        if (this.payWayIndex === -1) { // 
          this.$Toast('');
        } else {
          // 
          if (this.payWayIndex === 0) { // 
            tips = ` ${integral * buyNum} ${title}`;
          } else if (this.payWayIndex === 1) { // 
            tips = ` ${mixMoney * buyNum}  ${mixIntegral * buyNum} ${title}`;
          } else { // 
            tips = ` ${money * buyNum} ${title}`;
          }

          try {
            // 
            await this.$MessageBox.confirm(tips, '');

            if (this.payWayIndex === 0) { // 
              this.integralPay();
            } else { // 
              this.mixPay();
            }
          } catch (error) {
            console.log('error', error);
          }
        }
      },
      200,
      {
        leading: false,
        trailing: true,
      },
    ),

    // 
    async integralPay() {
      try {
        // 
        await service.exchangeWxCardLesson({
          buyNum: this.$route.params.buyNum,
          id: this.standard.id,
        });

        await this.$MessageBox.alert('');
        this.$router.push({ name: 'coupons' });
      } catch (error) {
        console.log('error', error);
      }
    },

    // /
    async mixPay() {
      const {
        mixIntegral,
        mixMoney,
        money,
      } = this.productData.info;
      const allIntegral = window.parseInt(this.productData.allIntegral);

      // 
      if (this.payWayIndex === 1 && allIntegral < mixIntegral * this.$route.params.buyNum) {
        this.$Toast('');
        return;
      }

      // 
      const istest = process.env.IS_TEST;
      const bid = process.env.BID;
      const externalBid = process.env.EXTERNAL_BID;
      const apiEnv = process.env.API_ENV;
      const testApi = process.env.TEST_API; // 
      const onLineApi = process.env.ONLINE_API; // 
      const apiURL = apiEnv === 'test' ? testApi : onLineApi;
      const href = window.location.href.slice(0, window.location.href.indexOf('/#/'));

      const attach = {
        bid: istest === 'yes' ? externalBid : bid,
        id: this.standard.id,
        payType: this.payWayIndex === 1 ? 2 : 1, // 1:  2
        userId: this.uid,
        buyNum: this.$route.params.buyNum,
      };

      try {
        const data = await service.mixPay({
          openid: istest === 'yes' ? 'oKPuYjvxCaklKurNUF6zcLfe_EY4' : this.productData.openid,
          body: '',
          total_fee: this.payWayIndex === 1 ? mixMoney : money, // 
          notify_url: `${apiURL}/Webapi/ShopTeach/weixinpayreturn`, // 
          attach: JSON.stringify(attach),
          reurl: `${href}/#/coupons?bid=${window.$bid}`,
        });

        window.location.href = data.data.url;
      } catch (error) {
        console.log('error', error);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@include b(payWay) {
  border-top: rem(20) solid #f0eff4;
  line-height: rem(84);
  font-size: rem(26);
  background-color: #fff;

  @include e(row) {
    padding: 0 rem(26);
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }

  @include e(leftTitle) {
    font-weight: bold;
  }
}
</style>



